
import { computed, defineComponent } from 'vue';
import emitter from '@/bus';
export default defineComponent({
  props: {
    header: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  setup(prop) {
    const imgLoad = () => {
      emitter.emit('imgLoad');
    };
    const address = computed(() => {
      const ads = prop.header.address.split('/');
      return ads[0] + ads[1];
    });
    return {
      imgLoad,
      address
    };
  }
});
