
import emitter from '@/bus';
import { defineComponent, computed } from 'vue';
export default defineComponent({
  props: {
    shopDetail: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  setup(props) {
    const imgLoad = () => {
      emitter.emit('imgLoad');
    };
    const image = computed(() => {
      return props.shopDetail.imagePath?.split('|');
    });
    return {
      imgLoad,
      image
    };
  }
});
